/* a, a:visited, a:hover, a:active, a:link {
  text-decoration: none;
} */

.text-link,
.text-link:hover {
	color: #fff;
	text-decoration: none;
}

.nav-color {
	background-color: #fff;
	z-index: 2;
}

.text-bolder {
	font-weight: 700;
}

.text-boldest {
	font-weight: 900;
}

.color-customBlue {
	color: #091292 !important;
}

.color-customBlueLight {
	color: #8d81a5 !important;
}

.color-customBlueLighter {
	color: #a299b6;
}

.bg-customBlue {
	background-color: #091292 !important;
}

.bg-skyBlue {
	background-color: skyblue !important;
}

.align-self-center {
	align-self: center;
}

.b-logo {
	width: 40px;
	background-color: #071b4b;
	height: 40px;
	text-align: center;
	/* padding-top: -15px; */
	font-size: 28px;
	border-radius: 10px;
}

.navLink {
	min-width: 100px;
}

.skillsImg {
	width: 80px;
	transition: width 0.15s;
	transition-timing-function: ease-in-out;
	cursor: pointer;
}

.skillsImg:hover {
	width: 90px;
	filter: none;
}

.justify-content-evenly {
	justify-content: space-evenly;
}

/* Projects page */

.proj-image {
	border-radius: 20px;
}

.proj-opacity {
	animation: opacityChange 2s;
}

/* Contact page */

.contact-btns span {
	max-width: 0;
	-webkit-transition: max-width 1s;
	transition: max-width 1s;
	display: inline-block;
	vertical-align: top;
	white-space: nowrap;
	overflow: hidden;
}

.contact-btns:hover span {
	max-width: 7rem;
}

.contact-btns:hover span::before {
	content: '|';
	padding-left: 5px;
	padding-right: 5px;
	color: #ffffff;
}

.particle-container {
	position: relative;
	background-color: #fff;
	z-index: -1;
	// width: 100vw;
	/* height: 90vh; */
}

.color-nameBlue {
	color: #000a94 !important;
}

.iconCard {
	height: 100px;
	width: 90px;
}

.contactImg {
	width: 35%;
}

.contactCard {
	animation: slidein 1.5s;
}

.contactCard:hover {
	cursor: pointer;
	box-shadow: 1px 1px 10px grey !important;
}

.about-text {
	letter-spacing: 1px;
	font-size: 18px;
	color: #727070;
}

.vertical-timeline::before {
	background: #bebebe !important;
}

.about-me-block {
	font-family: Fira Sans, sans-serif;
	font-weight: 700;
	font-size: 5.5rem;
	line-height: 120px;
	opacity: 0.1;
	color: #727070;
	text-transform: uppercase;
	text-align: right;
}

.skill-icons-container {
	animation: slidein 1.5s;
}

.timeline-work-icon {
	left: 26px !important;
}

.timeline-grad-icon {
	left: 23px !important;
}

@keyframes slidein {
	from {
		margin-top: 10%;
		opacity: 0;
	}
	to {
		margin-top: 0%;
		opacity: 1;
	}
}

@keyframes opacityChange {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

/* Media queries */

@media only screen and (min-width: 380px) and (max-width: 1180px) {
	.timeline-work-icon {
		left: 14px !important;
	}

	.timeline-grad-icon {
		left: 11px !important;
	}
}
